import React from 'react';
import { connect } from 'react-redux';
import { Section, Title, Content, OffsetHeader } from './section_styles';

class WeNeedAStack extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <div className="wh-100">
                    <Title>We Need A Stack</Title>
                    <Content>
                        <OffsetHeader>
                            "A Web stack is the collection of software required for Web development.
                            At a minimum, a Web stack contains an &nbsp;
                            <h3 className="inline">operating system (OS) <i className="fa fa-linux"/></h3>,
                            a <h3 className="inline">programming language <i className="fa fa-code"/></h3>,
                            <h3 className="inline">&nbsp;database <i className="fa fa-database"/></h3> software
                            and a Web <h3 className="inline">server <i className="fa fa-server"/></h3>."
                        </OffsetHeader>
                    </Content>
                </div>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(WeNeedAStack);



