import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import { Section, Title, Content, OffsetHeader } from './section_styles';

const Wrapper = styled.div`background-color: #B3293A;`;

class Technology extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <Wrapper className="wh-100">
                    <Title>Technology</Title>
                    <Content>
                        <OffsetHeader>Ultimately it's just important that we are creating</OffsetHeader>
                        <OffsetHeader>programming is child's play, so why not do it</OffsetHeader>
                    </Content>
                </Wrapper>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(Technology);



