import { injectGlobal } from 'styled-components';
require('./fonts.scss');
import Theme from './theme';
import StyleClasses from './classes';

injectGlobal`
html,body{
    width: 100%;
    height: 100%;
    margin: 0;
    color: white;
}
button:focus {outline:0;}
#app {
    width: 100%;
    height: 100%;
}
::-webkit-scrollbar {display: none;}
/* Font Families */
h1, h2, h3, a { font-family: 'Roboto-Thin'; font-weight: 100; }
h4, h5, h6, p, body { font-family: 'OpenSans-SemiBold'; }
h1, h2, h3, h4, h5, h6 { margin: 0 }
/* Font Sizes */
h1 { font-size: 65px; }
h2 { font-size: 40px; }
h4 { font-size: 36px; }
h5 { font-size: 28px; }
p { font-size: 18px; }
${StyleClasses}
`

export default Theme;



