import { combineReducers } from 'redux';
import { themeReducer } from './theme/theme.reducers';
import { currentSectionReducer, newSectionReducer, newSectionNameReducer } from './section/section.reducers';

/* root reducer */
export default combineReducers({
    theme: themeReducer,
    section: currentSectionReducer,
    newSection: newSectionReducer,
    newSectionName: newSectionNameReducer
});



