import React from 'react';
import { connect } from 'react-redux';
import { Section, Title, Content, OffsetHeader, Inset, Icon } from './section_styles';

class Backend extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <div className="wh-100">
                    <Title>Backend</Title>
                    <Content>
                        <OffsetHeader>Backend Refers to programs running on a private server.</OffsetHeader>
                        <OffsetHeader>Typically backend servers will:</OffsetHeader>
                        <Inset>
                            <h2>Run Computations</h2>
                            <h2>Handle Secure Data</h2>
                            <h2>Connect to other services</h2>
                        </Inset>
                        <Icon><i className="fa fa-server" /></Icon>
                    </Content>
                </div>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(Backend);



