import React from 'react';
import { connect } from 'react-redux';
import { Section, Title, Content, OffsetHeader, Inset } from './section_styles';

class ReactSection extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <div className="wh-100">
                    <Title>React</Title>
                    <Content>
                        <OffsetHeader>React is a scaleable and fast library for JS</OffsetHeader>
                        <Inset>
                            <h2>Renders a virtual DOM</h2>
                            <h2>Components recieve props to update</h2>
                            <h2>Components can manange state</h2>
                            <h2>modular and scaleable</h2>
                        </Inset>
                    </Content>
                </div>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(ReactSection);



