import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import { Section, Title, Content, OffsetHeader } from './section_styles';

const Flexbox = styled.div`
    margin-top: 30px;
    display: flex;
    justify-content: space-around;
`;

const Mongo = styled.img`
    display: block;
    position: relative;
    width: 300px;
    height: 100px;
`;

const Elastic = styled.img`
    display: block;
    position: relative;
    width: 100px;
    height: 100px;
`;

const Redis = styled.img`
    display: block;
    position: relative;
    width: 100px;
    height: 100px;
`;

const Postgres = styled.img`
    display: block;
    position: relative;
    width: 100px;
    height: 100px;
`;

const Mysql = styled.img`
    display: block;
    position: relative;
    width: 300px;
    height: 100px;
`;

class PopularDBs extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <div className="wh-100">
                    <Title>Popular DBs</Title>
                    <Content>
                        <OffsetHeader>NoSQL Databases</OffsetHeader>
                        <Flexbox>
                            <a href="https://docs.mongodb.com" target="_blank"><Mongo src="/assets/mongo.png" /></a>
                            <a href="https://www.elastic.co/guide/en/elasticsearch/reference/current/index.html" target="_blank"><Elastic src="/assets/elastic.png" /></a>
                            <a href="https://redis.io" target="_blank"><Redis src="/assets/redis.png" /></a>
                        </Flexbox>
                        <OffsetHeader>Relational Databases</OffsetHeader>
                        <Flexbox>
                            <a href="https://www.postgresql.org" target="_blank"><Postgres src="/assets/postgres.png" /></a>
                            <a href="https://www.mysql.com" target="_blank"><Mysql src="/assets/mysql.png" /></a>
                        </Flexbox>
                    </Content>
                </div>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(PopularDBs);



