import React from 'react';
import { connect } from 'react-redux';
import { Section, Title, Content, OffsetHeader } from './section_styles';

class StyleFlexbox extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <div className="wh-100">
                    <Title>Style - Flexbox</Title>
                    <Content>
                        <OffsetHeader>I use this website for all of my styles:</OffsetHeader>
                        <OffsetHeader>Check it out <a href="http://cssreference.io" target="_blank">http://cssreference.io</a>!</OffsetHeader>
                    </Content>
                </div>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(StyleFlexbox);



