import React from 'react';
import { connect } from 'react-redux';
import { Section, Title, Content, OffsetHeader } from './section_styles';

class Npm extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <div className="wh-100">
                    <Title>npm (Node Package Manager)</Title>
                    <Content>
                        <OffsetHeader>NPM installs modules</OffsetHeader>
                        <OffsetHeader>JS can import other files, and NPM makes public code easy to download for importing</OffsetHeader>
                    </Content>
                </div>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(Npm);



