import React from 'react';
import ReactDOM from 'react-dom';
import { connect } from 'react-redux';
import styled from 'styled-components';
import keydown from 'react-keydown';
require('smoothscroll-polyfill').polyfill();
import { SET_DEFAULT_SECTION, SET_CURRENT_SECTION } from './store';
import * as sections from './sections';
import { elements } from './constants';

const Wrapper = styled.div`
    width: 100%;
    height: 2000%;
    background: linear-gradient(
        to bottom,
        #007991 0%,
        #5DC7A9 10%,
        #7474BF 60%,
        #B3293A 100%
    );
`

class Content extends React.Component {
    constructor() {
        super();
        this.handleWheel = this.handleWheel.bind(this);
        this.upArrow = this.upArrow.bind(this);
        this.downArrow = this.downArrow.bind(this);
        this.scrollToSection = this.scrollToSection.bind(this);
        this.insts = {};
    }

    componentDidMount() {
        this.props.setDefaultSection(parseInt(window.pageYOffset / window.innerHeight));
        this.currentElement = parseInt(window.pageYOffset / window.innerHeight);
    }

    componentWillReceiveProps(nextProps) {
        if (nextProps.newSectionName != '' && !this.scrolling) {
            this.scrollToSection(this.insts[nextProps.newSectionName].getWrappedInstance().props.id)

        } else if (nextProps.newSection != nextProps.currentSection) {
            this.scrollToSection(nextProps.newSection);
        }
    }

    handleWheel(e) {
        e.preventDefault()
        if (e.deltaY > 10) this.downArrow(e)
        else if (e.deltaY < -10) this.upArrow(e);
        return false;
    }

    @keydown('up')
    upArrow(e) {
        e.preventDefault();
        this.scrollToSection(this.props.currentSection - 1);
    }

    @keydown('down')
    downArrow(e) {
        e.preventDefault()
        this.scrollToSection(this.props.currentSection + 1);
    }

    scrollToSection(section) {
      if (!this.scrolling && section >= 0 && section < elements.length) {
          this.props.setCurrentSection(section)
          this.scrolling = true;
          setTimeout(()=>{ this.scrolling = false }, 750)
          ReactDOM.findDOMNode(this.insts[elements[section]].getWrappedInstance()).scrollIntoView({ behavior: 'smooth' })
      }
    }

    render() {
        return (
            <Wrapper ref={inst => {this.wrapper = inst}} onClick={this.scrollDown} onWheel={this.handleWheel}>
                {elements.map((element, index) => {
                    let Section = sections[element];
                    return <Section ref={inst => {this.insts[element] = inst}} parent={this} key={index} id={index} />
                })}
            </Wrapper>
        );
    }
}

export default connect(state => ({
  currentSection: state.section,
  newSection: state.newSection,
  newSectionName: state.newSectionName
}), dispatch => ({
  setDefaultSection: section => { dispatch({ type: SET_DEFAULT_SECTION, section }) },
  setCurrentSection: section => { dispatch({ type: SET_CURRENT_SECTION, section })}
}))(Content);



