import React from 'react';
import { connect } from 'react-redux';
import { Section, Title, Content, OffsetHeader, Inset } from './section_styles';

class SetupApi extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <div className="wh-100">
                    <Title>Setup API</Title>
                    <Content>
                        <OffsetHeader>Modern APIs conform to a protocol called REST</OffsetHeader>
                        <OffsetHeader>RESTful APIs have these interactions:</OffsetHeader>
                        <Inset>
                            <h2>POST (Add)</h2>
                            <h2>PUT (Update)</h2>
                            <h2>GET</h2>
                            <h2>DELETE</h2>
                        </Inset>
                    </Content>
                </div>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(SetupApi);



