import { SET_DEFAULT_SECTION, SET_CURRENT_SECTION, GOTO_SECTION, GOTO_SECTION_NAME } from './section.actions';


export function currentSectionReducer(state = null, action) {
  switch (action.type) {
    case SET_DEFAULT_SECTION:
        return action.section;
    case SET_CURRENT_SECTION:
        return action.section;
    default:
        return state;
  }
}

export function newSectionReducer(state = 0, action) {
    switch (action.type) {
        case GOTO_SECTION:
            return action.section;
        case SET_CURRENT_SECTION:
            return action.section;
        default:
            return state;
    }
}

export function newSectionNameReducer(state = '', action) {
    switch (action.type) {
        case GOTO_SECTION_NAME:
            return action.name;
        case SET_CURRENT_SECTION:
            return '';
        default:
            return state;
    }
}



