import React from 'react';
import { connect } from 'react-redux';
import styled, { keyframes } from 'styled-components';
import { Section } from './section_styles';
import { GOTO_SECTION } from '../store';

const fadeOut = keyframes`from { opacity: 1; } to { opacity: 0; }`
const Title = styled.h1`display: block; margin-top: -5%; text-align: center;`;
const Arrow = styled.img`fill: white; position: absolute; bottom: 20%; width: 50px;
    animation-name: ${fadeOut};
    animation-duration: 1s;
    animation-direction: alternate;
    animation-iteration-count: infinite;
`;
const Wrapper = styled(Section)`cursor: pointer;`;

class LetsMakeAWebsite extends React.Component {
    render() {
        return (
            <Wrapper id={this.props.id} onClick={() => {
                if (!this.props.parent.scrolling) {
                        this.props.dispatch({
                        type: GOTO_SECTION,
                        section: this.props.currentSection + 1
                    })
                }}}>
                <div className="flex-center wh-100">
                    <Title>Let's Make A Website</Title>
                    <Arrow src="assets/downArrow.png"/>
                </div>
            </Wrapper>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(LetsMakeAWebsite);



