import React from 'react';
import { connect } from 'react-redux';
import { Section, Title, Content, OffsetHeader, Inset } from './section_styles';

class Golang extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <div className="wh-100">
                    <Title>Golang</Title>
                    <Content>
                        <OffsetHeader>Go is my server-side language of choice because:</OffsetHeader>
                        <Inset>
                            <h2>Simple syntax</h2>
                            <h2>Runs fast</h2>
                            <h2>Compiles fast</h2>
                            <h2>Core Http Packages</h2>
                        </Inset>
                    </Content>
                </div>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(Golang);



