import React from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import { Section, Title, Content } from './section_styles';

const Flexbox = styled.div`
    margin-top: 30px;
    display: flex;
    justify-content: space-around;
    flex-wrap: wrap;
    max-width: 100%;
`;

const Logo = styled.img`
    display: block;
    position: relative;
    width: 150px;
    height: 150px;
    margin: 30px;
`;

const Node = styled.img`
    display: block;
    position: relative;
    width: 300px;
    height: 150px;
    margin: 30px;
`;

class ServerSideLanguages extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <div className="wh-100">
                    <Title>Server-Side Languages</Title>
                    <Content>
                        <Flexbox>
                            <a href="https://golang.org" target="_blank"><Logo src="/assets/golang.png" /></a>
                            <a href="https://www.python.org" target="_blank"><Logo src="/assets/python.png" /></a>
                            <a href="http://www.cplusplus.com" target="_blank"><Logo src="/assets/cpp.png" /></a>
                            <a href="https://docs.microsoft.com/en-us/dotnet/csharp/csharp" target="_blank"><Logo src="/assets/dotnet.png" /></a>
                            <a href="https://www.rust-lang.org" target="_blank"><Logo src="/assets/rust.png" /></a>
                            <a href="https://nodejs.org" target="_blank"><Node src="/assets/nodejs.png" /></a>
                        </Flexbox>
                    </Content>
                </div>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(ServerSideLanguages);



