import React, { Component } from 'react';
import { connect } from 'react-redux';
import styled from 'styled-components';
import { GOTO_SECTION_NAME } from './store';
import { elements } from './constants';

const Wrapper = styled.div`
    position: fixed;
    left: 20px;
    top: calc(50% - 275px);
    margin: auto;
    z-index: 99;
    opacity: 0;
    pointer-events: none;
    cursor: default;

    ${props => props.active && `
        opacity: 1;
        transition: opacity 1s;
        pointer-events: auto;
    `}
`

const Link = styled.a`
    cursor: pointer;
    display: block;
    font-size: ${props => props.large ? '25px' : '20px'};
    margin: 3px;
    margin-left: ${props => props.indent ? '15px' : 0};
    padding: 0;
    &:hover {
        font-weight: bold;
        transform-origin: left;
        transform: scale(1.5);
    }
    ${props => props.active && `
        font-weight: bold;
        transform-origin: left;
        transform: scale(1.5);
        margin-bottom: 10px;
        margin-top: 10px;
    `}
`

class Menu extends Component {

    shouldComponentUpdate(nextProps) {
        if (nextProps.currentSection != this.props.currentSection) return true;
    }

    render() {
        return (
            <Wrapper active={this.props.currentSection !== 0}>
                {elements.map((name, idx) => {
                    return (
                        <Link
                            large={idx===0}
                            indent={idx!==0}
                            active={this.props.currentSection === idx}
                            onClick={() => {this.props.goto(name)}}
                        >
                            {idx === 0 && <i className="fa fa-home"/>}
                            {' '}{name}
                        </Link>
                    );
                })}
            </Wrapper>
        );
    }

}

export default connect(state=>({
    currentSection: state.section
}), dispatch => ({
    goto: name => { dispatch({ type: GOTO_SECTION_NAME, name }) }
}))(Menu);



