import React from 'react';
import { connect } from 'react-redux';
import { Section, Title, Content, OffsetHeader, Inset } from './section_styles';

class Packages extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <div className="wh-100">
                    <Title>Packages</Title>
                    <Content>
                        <OffsetHeader>Go has a large community building packages</OffsetHeader>
                        <Inset>
                            <h2>Most of them are at <a href="https://godoc.org/" target="_blank">https://godoc.org</a></h2>
                        </Inset>
                        <OffsetHeader>Packages I frequently use are &nbsp;
                            <a href="https://github.com/gin-gonic/gin" target="_blank">gin</a> and &nbsp;
                            <a href="https://github.com/jmoiron/sqlx" target="_blank">sqlx</a>
                        </OffsetHeader>
                    </Content>
                </div>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(Packages);



