import React from 'react';
import { connect } from 'react-redux';
import { Section, Title, Content, OffsetHeader } from './section_styles';

class Postman extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <div className="wh-100">
                    <Title>Postman</Title>
                    <Content>
                        <OffsetHeader>Postman is a sweet tool for testing an API</OffsetHeader>
                        <OffsetHeader>Checkout Postman <a href="https://www.getpostman.com/apps" target="_blank">here</a>!</OffsetHeader>
                    </Content>
                </div>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(Postman);



