import React from 'react';
import { connect } from 'react-redux';
import { Section, Title, Content, OffsetHeader, Inset } from './section_styles';

class LibrariesFrameworks extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <div className="wh-100">
                    <Title>Libraries/Frameworks</Title>
                    <Content>
                        <OffsetHeader>New Libraries are becoming essential in development</OffsetHeader>
                        <Inset>
                            <h2>React</h2>
                            <h2>AngularJS</h2>
                            <h2>Vue</h2>
                            <h2>JQuery</h2>
                            <h2>Django</h2>
                        </Inset>
                    </Content>
                </div>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(LibrariesFrameworks);



