import React from 'react';
import { connect } from 'react-redux';
import { Section, Title, Content, OffsetHeader, Inset } from './section_styles';

class Frontend extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <div className="wh-100">
                    <Title>Frontend</Title>
                    <Content>
                        <OffsetHeader>Broken down.. the frontend has few responsibilities</OffsetHeader>
                        <Inset>
                            <h2>Fetch data</h2>
                            <h2>Display information</h2>
                            <h2>Cache data</h2>
                        </Inset>
                    </Content>
                </div>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(Frontend);



