import React from 'react';
import { connect } from 'react-redux';
import { Section, Title, Content, OffsetHeader, Inset } from './section_styles';

class WebsiteGoals extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <div className="wh-100">
                    <Title>Website Goals</Title>
                    <Content>
                        <OffsetHeader>Need to provide reliable user experience to achieve some goal.</OffsetHeader>
                        <br />
                            <h4>Typically means:</h4>
                            <Inset>
                                <h2>store data</h2>
                                <h2>run calculations</h2>
                                <h2>provide user interactions</h2>
                            </Inset>
                    </Content>
                </div>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(WebsiteGoals);



