import React from 'react';
import { connect } from 'react-redux';
import { Section, Title, Content, OffsetHeader, Inset } from './section_styles';

class Redux extends React.Component {
    render() {
        return (
            <Section id={this.props.id}>
                <div className="wh-100">
                    <Title>Redux</Title>
                    <Content>
                        <OffsetHeader>Redux creates a global store for an entire app</OffsetHeader>
                        <Inset>
                            <h2>takes logic responsibilities off Components</h2>
                            <h2>Components just need to dispatch actions and display props</h2>
                        </Inset>
                    </Content>
                </div>
            </Section>
        )
    }
}

export default connect(state => ({
    currentSection: state.section
}), null, null, { withRef: true })(Redux);



