import styled from 'styled-components';

export const Section = styled.section`
    position: relative;
    height: 5%;
    width: 100%;
`
export const Title = styled.h2`display: block; position: relative; text-align: center; top: 5px; border-bottom: 1px solid white;`;

export const Content = styled.div`
    width: calc(100% - 400px);
    max-width: 900px;
    left: 325px;
    position: relative;
    display: flex;
    flex-direction: column;
`
export const OffsetHeader = styled.h4`
    margin-top: 50px;
`
export const Inset = styled.div`
    margin-left: 30px;
`
export const Icon = styled.h1`
    margin: 30px;
`



